import { useEffect, useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";

type HiringStage = "new" | "screening" | "interview" | "hired" | "rejected";

type Applicant = {
  id: string;
  name: string;
  email: string;
  phone?: string;
  role: string;
  source: string;
  stage: HiringStage;
  createdAt: string;
};

const STORAGE_KEY = "hiringApplicants";

const stageLabel: Record<HiringStage, string> = {
  new: "New",
  screening: "Screening",
  interview: "Interview",
  hired: "Hired",
  rejected: "Rejected",
};

export default function OnboardingTab() {
  const [applicants, setApplicants] = useState<Applicant[]>([]);
  const [filterStage, setFilterStage] = useState<"all" | HiringStage>("all");
  const [newApplicant, setNewApplicant] = useState({
    name: "",
    email: "",
    role: "",
    source: "Manual",
  });

  useEffect(() => {
    if (typeof window === "undefined") return;
    try {
      const stored = JSON.parse(localStorage.getItem(STORAGE_KEY) || "[]");
      if (Array.isArray(stored)) {
        setApplicants(stored);
      }
    } catch {
      // ignore
    }
  }, []);

  useEffect(() => {
    if (typeof window === "undefined") return;
    localStorage.setItem(STORAGE_KEY, JSON.stringify(applicants));
  }, [applicants]);

  const handleAddApplicant = () => {
    if (!newApplicant.name.trim() || !newApplicant.email.trim() || !newApplicant.role.trim()) return;
    const now = new Date().toISOString();
    const applicant: Applicant = {
      id: `AP-${Date.now()}`,
      name: newApplicant.name.trim(),
      email: newApplicant.email.trim(),
      role: newApplicant.role.trim(),
      source: newApplicant.source,
      stage: "new",
      createdAt: now,
    };
    setApplicants((prev) => [applicant, ...prev]);
    setNewApplicant({ name: "", email: "", role: "", source: "Manual" });
  };

  const updateStage = (id: string, stage: HiringStage) => {
    setApplicants((prev) => prev.map((a) => (a.id === id ? { ...a, stage } : a)));
  };

  const filtered = filterStage === "all" ? applicants : applicants.filter((a) => a.stage === filterStage);

  const stageColor = (stage: HiringStage) => {
    switch (stage) {
      case "new":
        return "bg-sky-100 text-sky-700";
      case "screening":
        return "bg-amber-100 text-amber-700";
      case "interview":
        return "bg-indigo-100 text-indigo-700";
      case "hired":
        return "bg-emerald-100 text-emerald-700";
      case "rejected":
        return "bg-rose-100 text-rose-700";
      default:
        return "";
    }
  };

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Onboarding pipeline</CardTitle>
          <CardDescription>
            Capture new applicants and move them through your hiring stages â€“ similar to a simple CRM for hiring.
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* Quick add applicant */}
          <div className="grid gap-3 md:grid-cols-[1.5fr_1.5fr_1.5fr_1fr_auto]">
            <Input
              placeholder="Applicant name"
              value={newApplicant.name}
              onChange={(e) => setNewApplicant((p) => ({ ...p, name: e.target.value }))}
            />
            <Input
              placeholder="Email"
              value={newApplicant.email}
              onChange={(e) => setNewApplicant((p) => ({ ...p, email: e.target.value }))}
            />
            <Input
              placeholder="Role (e.g. Field Cleaner)"
              value={newApplicant.role}
              onChange={(e) => setNewApplicant((p) => ({ ...p, role: e.target.value }))}
            />
            <Input
              placeholder="Source (e.g. Indeed, FB Ads)"
              value={newApplicant.source}
              onChange={(e) => setNewApplicant((p) => ({ ...p, source: e.target.value }))}
            />
            <Button onClick={handleAddApplicant} className="whitespace-nowrap">
              Add
            </Button>
          </div>

          {/* Filters */}
          <div className="flex items-center justify-between gap-3 flex-wrap">
            <p className="text-sm text-muted-foreground">
              {filtered.length} applicant{filtered.length === 1 ? "" : "s"} shown
            </p>
            <Select
              value={filterStage}
              onValueChange={(v: "all" | HiringStage) => setFilterStage(v)}
            >
              <SelectTrigger className="w-40">
                <SelectValue placeholder="Filter by stage" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">All stages</SelectItem>
                <SelectItem value="new">New</SelectItem>
                <SelectItem value="screening">Screening</SelectItem>
                <SelectItem value="interview">Interview</SelectItem>
                <SelectItem value="hired">Hired</SelectItem>
                <SelectItem value="rejected">Rejected</SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/* Table */}
          <div className="overflow-x-auto rounded-md border">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Applicant</TableHead>
                  <TableHead>Role</TableHead>
                  <TableHead>Source</TableHead>
                  <TableHead>Stage</TableHead>
                  <TableHead className="text-right">Move to</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filtered.length === 0 && (
                  <TableRow>
                    <TableCell colSpan={5} className="text-center text-sm text-muted-foreground">
                      No applicants yet. Add your first one above.
                    </TableCell>
                  </TableRow>
                )}
                {filtered.map((a) => (
                  <TableRow key={a.id}>
                    <TableCell>
                      <div className="space-y-0.5">
                        <div className="font-medium">{a.name}</div>
                        <div className="text-xs text-muted-foreground">{a.email}</div>
                      </div>
                    </TableCell>
                    <TableCell>{a.role}</TableCell>
                    <TableCell className="text-sm text-muted-foreground">{a.source}</TableCell>
                    <TableCell>
                      <Badge className={stageColor(a.stage)} variant="outline">
                        {stageLabel[a.stage]}
                      </Badge>
                    </TableCell>
                    <TableCell className="text-right space-x-1">
                      {(["new", "screening", "interview", "hired", "rejected"] as HiringStage[]).map(
                        (stage) => (
                          <Button
                            key={stage}
                            variant={a.stage === stage ? "default" : "outline"}
                            size="xs"
                            onClick={() => updateStage(a.id, stage)}
                          >
                            {stageLabel[stage]}
                          </Button>
                        )
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
